import React from "react";
import "../Assets/Stylesheets/Components/ProjectCard.css";
import Figure from "react-bootstrap/Figure";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"
import ProjectPictureModal from "Components/ProjectPictureModal"

interface IProjectCardState
{
    showModal: boolean;
}

interface IProjectCardProps
{
    imgSrc: string;
    projectName: string;
    gitHubLink:string;
    demoPics:any;
}

class ProjectCard extends React.Component<IProjectCardProps, IProjectCardState>
{
    constructor(props:IProjectCardProps)
    {
        super(props)
        this.state =
        {
            showModal: false
        }

        this.ToggleModal = this.ToggleModal.bind(this);
    }

    render()
    {
        return(
            <>
            <Figure>
                <div className="proj-figure">
                    <Figure.Image
                        height={200}
                        width={400}
                        alt={this.props.projectName}
                        src={this.props.imgSrc}
                    />
                    <Container className="center-container">
                        <Row>
                            <Col>
                        <div className="center-btns-in-row">
{/* eslint-disable-next-line*/}
                        <a onClick={this.ToggleModal} rel="noopener noreferrer">
                            <div className="text">
                                Demo
                            </div>
                        </a>
                    </div>
                    </Col>
                    <Col>
                        <div className="center-btns-in-row">
                        <a href={this.props.gitHubLink} style={{textDecoration:"none"}} target="_blank" rel="noopener noreferrer">
                            <div className="text">
                                Code
                            </div>
                        </a>
                    </div>
                    </Col>
                    </Row>
                    </Container>
                </div>
                <Figure.Caption>
                    {this.props.projectName}
                </Figure.Caption>
            </Figure>

            <ProjectPictureModal 
                show={this.state.showModal} 
                toggleModal={this.ToggleModal} 
                projectName={this.props.projectName}
                demoPics={this.props.demoPics}
                />
            </>
        )
    }

    private ToggleModal(event:any) {
        this.setState({showModal:!this.state.showModal})
    }
}
export default ProjectCard;